const ReactDOM = {
  render
}

function render(vnode, container) {
  console.log(vnode)

  // Â¶ÇÊûúvodeÊòØundefinedÔºåÂ∞±‰ªÄ‰πàÈÉΩ‰∏çÂÅö
  if (vnode === undefined) return

  // Â¶ÇÊûúÊòØÂ≠óÁ¨¶‰∏≤ÂàôÁõ¥Êé•Ê∏≤ÊüìÂà∞container
  if (typeof vnode === 'string') {
    const textNode = document.createTextNode(vnode)
    return container.appendChild(textNode)
  }

  // Âê¶ÂàôÂ∞±ÊòØËôöÊãüdomÂØπË±°
  const { tag, attrs } = vnode
  // ÂàõÂª∫ËäÇÁÇπÂØπË±°
  const dom = document.createElement(tag)

  if (attrs) {
    // Â¶ÇÊûúÊúâÂ±ûÊÄß
    Object.keys(attrs).forEach((key) => {
      const value = attrs[key]
      setAttribute(dom, key, value)
    })
  }

  // ÈÄíÂΩíÊ∏≤ÊüìÂ≠êËäÇÁÇπ
  vnode.childrens.forEach((child) => {
    render(child, dom)
  })

  return container.appendChild(dom)

}

function setAttribute(dom, key, value) {

  // Â∞ÜÂ±ûÊÄßÂêçclassNameËΩ¨Êç¢‰∏∫class
  if (key === 'className') {
    key = 'class'
  }

  // Â¶ÇÊûúÊòØ‰∏™‰∫ã‰ª∂
  if (/on\w+/.test(key)) {
    // ËΩ¨ÊàêÂ∞èÂÜô
    key = key.toLowerCase()
    dom[key] = value || ''

  } else if (key === 'style') {
    if (!value || typeof value === 'string') {
      dom.style.cssText = value || '';
    } else if (value && typeof value === 'object') {

      for (let k in value) {
        if (typeof value[key] === 'number') {
          dom.style[key] = value[k] + 'px'
        } else {
          dom.style[k] = value[k]
        }
      }
    }
  } else {

    // ÂÖ∂‰ªñÂ±ûÊÄß
    if (key in dom) {
      dom[key] = value || ''
    }
    if (value) {
      dom.setAttribute(key, value)
    } else {
      dom.removeAttribute(key)
    }
  }
  
}

export default ReactDOM